import { createMiddleware } from 'redux-beacon';
import GoogleTagManager from '@redux-beacon/google-tag-manager';
import { omit } from 'lodash';
import { api, checkout } from '../actions';

const eventsMap = {
    [api.auth.login.successType]: () => ({
        event: 'login',
        method: 'email',
    }),
    [api.users.create.successType]: () => ({
        event: 'sign_up',
        method: 'email',
    }),
    [api.users.create.failureType]: (action) => ({
        event: 'Sign up error',
        error: omit(action.error, ['stack', 'json']),
    }),
    [api.websites.update.successType]: (action, prevState, nextState) => {
        if (
            !prevState.websites.find((website) => website.slug === action.payload.slug)?.enabled &&
            action.payload.enabled
        ) {
            return {
                event: 'website_published',
            };
        }
    },
    [api.listenSubscriptionCreated.type]: (action) => ({
        event: 'subscription_created',
        ...action.payload,
    }),
    [api.listenSubscriptionCancelled.type]: () => ({
        event: 'subscription_cancelled',
    }),
    [api.users.me.successType]: (action) => ({
        event: 'account_load',
        userId: action.result._id,
        email: action.result.email,
    }),
    [api.workspaces.retrieve.successType]: (action) => ({
        event: 'workspace_load',
        workspaceId: action.result._id,
        plan: action.result.billing.plan,
    }),
    [checkout.applyCoupon.type]: (action) => ({
        event: 'checkout.applyCoupon',
        coupon: action.payload.coupon,
    }),
    [checkout.changeCountry.type]: (action) => ({
        event: 'checkout.changeCountry',
        country: action.payload.country,
    }),
    [checkout.close.type]: () => ({
        event: 'checkout.close',
    }),
    [checkout.complete.type]: () => ({
        event: 'checkout.complete',
    }),
    [checkout.completePayment.type]: () => ({
        event: 'checkout.completePayment',
    }),
    [checkout.load.type]: () => ({
        event: 'checkout.load',
    }),
    [checkout.error.type]: (action) => ({
        event: 'checkout.error',
        error: action.payload.error,
    }),
    [checkout.selectPaymentMethod.type]: (action) => ({
        event: 'checkout.selectPaymentMethod',
        paymentMethod: action.payload.payment_method,
    }),
    '*': (action) => ({
        event: 'reduxAction',
        type: action.type,
    }),
};

const gtm = GoogleTagManager();
const logger = process.env.NODE_ENV === 'development' ? require('@redux-beacon/logger').default : null;

export const gtmMiddleware = createMiddleware(eventsMap, gtm, { logger });
