import { get } from 'lodash';

import { mediaUtils } from '../helpers';
import {
    ImageBlockKind,
    ImageGalleryBlockKind,
    HatBlockKind,
    FeaturesBlockKind,
    ServicesGridBlockKind,
    LinkBlockKind,
    SocialsBlockKind,
    AudioBlockKind,
    VideoBlockKind,
    VideoGalleryBlockKind,
    QuoteBlockKind,
    AccordionBlockKind,
    HeadingBlockKind,
    TextBlockKind,
    ListBlockKind,
    MessengersBlockKind,
    CustomBlockKind,
} from './blocks';

export const ContentAttributes = {
    Text: 1,
    Link: 2,
    Image: 4,
    Media: 8,
    Contact: 16,
    Embed: 32,
};

export const walkWebsiteAttributes = (website, consumer) => {
    const visit = (path, type) => {
        const value = get(website, path, null);

        return value && consumer(path, { value, type });
    };

    if (false === visit('embeddedCode', ContentAttributes.Embed)) {
        return false;
    }

    for (const [index, page] of website.pages.entries()) {
        if (false === walkPageAttributes(page, consumer, `pages.${index}`)) {
            return false;
        }
    }
};

export const walkPageAttributes = (page, consumer, prefix) => {
    const visit = (path, type) => {
        const realPath = prefix ? `${prefix}.${path}` : path;
        const value = get(page, realPath, null);

        return value && consumer(realPath, { value, type });
    };

    if (false === visit('seo.ogImageUrl', ContentAttributes.Image)) {
        return false;
    }

    if (false === visit('theme.page.backgroundImage', ContentAttributes.Image)) {
        return false;
    }

    for (const [blockIndex, block] of page.blocks.entries()) {
        const path = `blocks.${blockIndex}`;

        switch (block.kind) {
            case ImageBlockKind: {
                if (false === visit(`${path}.url`, ContentAttributes.Image)) {
                    return false;
                }

                break;
            }

            case ImageGalleryBlockKind: {
                for (const index of block.images.keys()) {
                    if (false === visit(`${path}.images.${index}.url`, ContentAttributes.Image)) {
                        return false;
                    }

                    if (false === visit(`${path}.images.${index}.annotation`, ContentAttributes.Text)) {
                        return false;
                    }
                }

                break;
            }
            case HatBlockKind: {
                if (false === visit(`${path}.bgUrl`, ContentAttributes.Image)) {
                    return false;
                }

                if (false === visit(`${path}.logoUrl`, ContentAttributes.Image)) {
                    return false;
                }

                if (false === visit(`${path}.title`, ContentAttributes.Text)) {
                    return false;
                }

                if (false === visit(`${path}.subtitle`, ContentAttributes.Text)) {
                    return false;
                }

                break;
            }

            case FeaturesBlockKind: {
                for (const [index, item] of block.items.entries()) {
                    if (
                        item.glyph.type === 'image' &&
                        false === visit(`${path}.items.${index}.glyph.url`, ContentAttributes.Image)
                    ) {
                        return false;
                    }

                    if (false === visit(`${path}.items.${index}.title`, ContentAttributes.Text)) {
                        return false;
                    }

                    if (false === visit(`${path}.items.${index}.content`, ContentAttributes.Text)) {
                        return false;
                    }
                }

                break;
            }

            case LinkBlockKind: {
                if (false === visit(`${path}.link.url`, ContentAttributes.Link)) {
                    return false;
                }

                if (false === visit(`${path}.link.title`, ContentAttributes.Text)) {
                    return false;
                }

                break;
            }

            case SocialsBlockKind: {
                for (const index of block.items.keys()) {
                    if (false === visit(`${path}.items.${index}.url`, ContentAttributes.Link)) {
                        return false;
                    }
                }

                break;
            }

            case ServicesGridBlockKind: {
                for (const [index, item] of block.items.entries()) {
                    if (false === visit(`${path}.items.${index}.url`, ContentAttributes.Link)) {
                        return false;
                    }

                    for (const photoIndex of item.photos.keys()) {
                        if (false === visit(`${path}.items.${index}.photos.${photoIndex}`, ContentAttributes.Image)) {
                            return false;
                        }
                    }

                    if (false === visit(`${path}.items.${index}.title`, ContentAttributes.Text)) {
                        return false;
                    }

                    if (false === visit(`${path}.items.${index}.description`, ContentAttributes.Text)) {
                        return false;
                    }
                }

                break;
            }

            case AudioBlockKind: {
                if (false === visit(`${path}.url`, ContentAttributes.Media)) {
                    return false;
                }

                break;
            }

            case VideoBlockKind: {
                if (false === visit(`${path}.url`, ContentAttributes.Media)) {
                    return false;
                }

                break;
            }

            case VideoGalleryBlockKind: {
                for (const index of block.videos.keys()) {
                    if (false === visit(`${path}.videos.${index}.url`, ContentAttributes.Media)) {
                        return false;
                    }

                    if (false === visit(`${path}.videos.${index}.annotation`, ContentAttributes.Text)) {
                        return false;
                    }
                }

                break;
            }

            case QuoteBlockKind: {
                if (false === visit(`${path}.author`, ContentAttributes.Text)) {
                    return false;
                }

                if (false === visit(`${path}.content`, ContentAttributes.Text)) {
                    return false;
                }

                break;
            }

            case AccordionBlockKind: {
                if (false === visit(`${path}.title`, ContentAttributes.Text)) {
                    return false;
                }

                if (false === visit(`${path}.content`, ContentAttributes.Text)) {
                    return false;
                }

                break;
            }

            case HeadingBlockKind:
            case TextBlockKind: {
                if (false === visit(`${path}.content`, ContentAttributes.Text)) {
                    return false;
                }

                break;
            }

            case ListBlockKind: {
                for (const index of block.items.keys()) {
                    if (false === visit(`${path}.items.${index}.content`, ContentAttributes.Text)) {
                        return false;
                    }
                }

                break;
            }

            case MessengersBlockKind: {
                for (const index of block.messengers.keys()) {
                    if (false === visit(`${path}.messengers.${index}.value`, ContentAttributes.Contact)) {
                        return false;
                    }
                }

                break;
            }
        }
    }
};

export const recursivelyVisitNestedBlocks = (page, block, consumer) => {
    const results = [];
    let processed = 0;

    switch (block.kind) {
        case CustomBlockKind: {
            for (const { blockId } of block.areas) {
                const orphan = page.blocks.find(({ _id }) => String(_id) === String(blockId));

                if (orphan) {
                    const { processed: p, results: r } = recursivelyVisitNestedBlocks(page, orphan, consumer);

                    processed += p;
                    results.push(...r);
                }
            }

            break;
        }
    }

    const result = consumer(block);

    if (result !== false) {
        processed++;

        if (result) {
            results.push(result);
        }
    }

    return {
        processed,
        results,
    };
};

export const UploadUsage = {
    Image: 'Image',
    Background: 'Background',
    Logo: 'Logo',
    Icon: 'Icon',
};

const makeUploadMatcher = ({ path }) => {
    const relative = mediaUtils.getRelativeUrl(path);

    return (other) => other && (path === other || relative === mediaUtils.getRelativeUrl(other));
};

const getImageUsageInBlock = (block, matchPath) => {
    switch (block.kind) {
        case ImageBlockKind: {
            return matchPath(block.url) && [{ entity: ImageBlockKind, as: UploadUsage.Image }];
        }
        case ImageGalleryBlockKind: {
            return block.images
                .map(({ url }) => matchPath(url) && { entity: ImageGalleryBlockKind, as: UploadUsage.Image })
                .filter(Boolean);
        }
        case HatBlockKind: {
            return [
                matchPath(block.bgUrl) && { entity: HatBlockKind, as: UploadUsage.Background },
                matchPath(block.logoUrl) && { entity: HatBlockKind, as: UploadUsage.Logo },
            ].filter(Boolean);
        }
        case FeaturesBlockKind: {
            return block.items
                .map(
                    (item) =>
                        item.glyph.type === 'image' &&
                        matchPath(item.glyph.url) && { entity: FeaturesBlockKind, as: UploadUsage.Icon }
                )
                .filter(Boolean);
        }
        case ServicesGridBlockKind: {
            return []
                .concat(
                    ...block.items.map((item) =>
                        item.photos.map(
                            (url) => matchPath(url) && { entity: ServicesGridBlockKind, as: UploadUsage.Image }
                        )
                    )
                )
                .filter(Boolean);
        }
    }
};

export const getUploadUsagesOnPage = (page, matchPath) => {
    const usages = [];

    if (matchPath(get(page, 'seo.ogImageUrl'))) {
        usages.push({ entity: 'page.og-image', as: UploadUsage.Image });
    }

    if (matchPath(get(page, 'theme.page.backgroundImage'))) {
        usages.push({ entity: 'page', as: UploadUsage.Background });
    }

    for (const block of page.blocks) {
        const found = getImageUsageInBlock(block, matchPath);

        if (found && found.length) {
            usages.push(...found);
        }
    }

    return usages;
};

export const getUploadUsagesOnWebsite = (upload, { pages, seo }) => {
    const matchPath = makeUploadMatcher(upload);
    const usages = [];

    if (matchPath(seo.ogImageUrl)) {
        usages.push({ entity: 'website.og-image', as: UploadUsage.Image });
    }

    for (const page of pages) {
        const found = getUploadUsagesOnPage(page, matchPath);

        if (found.length) {
            usages.push(...found);
        }
    }

    return usages;
};

export const isImageUsedOnPage = (page, matchPath) => {
    if (matchPath(get(page, 'seo.ogImageUrl'))) {
        return true;
    }

    if (matchPath(get(page, 'theme.page.backgroundImage'))) {
        return true;
    }

    return !!page.blocks.find((block) => {
        switch (block.kind) {
            case ImageBlockKind: {
                return matchPath(block.url);
            }
            case ImageGalleryBlockKind: {
                return !!block.images.find(({ url }) => matchPath(url));
            }
            case HatBlockKind: {
                return matchPath(block.bgUrl) || matchPath(block.logoUrl);
            }
            case FeaturesBlockKind: {
                return !!block.items.find((item) => item.glyph.type === 'image' && matchPath(item.glyph.url));
            }
            case ServicesGridBlockKind: {
                return !!block.items.find((item) => item.photos.find(matchPath));
            }
        }
    });
};

export const isUploadUsedOnPage = (upload, page) => {
    return isImageUsedOnPage(page, makeUploadMatcher(upload));
};

export const isUploadUsedOnWebsite = (upload, { pages, seo }) => {
    const matchPath = makeUploadMatcher(upload);

    if (matchPath(seo.ogImageUrl)) {
        return true;
    }

    for (const page of pages) {
        if (isImageUsedOnPage(page, matchPath)) {
            return true;
        }
    }
};

export const walkPageUrlAttributes = (page, consumer) => {
    let url;

    if ((url = get(page, 'seo.ogImageUrl')) && false === consumer('seo.ogImageUrl', url)) {
        return;
    }

    if ((url = get(page, 'theme.page.backgroundImage')) && false === consumer('theme.page.backgroundImage', url)) {
        return;
    }

    for (const [blockIndex, block] of page.blocks.entries()) {
        const path = `blocks.${blockIndex}`;

        switch (block.kind) {
            case ImageBlockKind: {
                if (block.url && false === consumer(`${path}.url`, block.url, ImageBlockKind)) {
                    return;
                }

                break;
            }
            case ImageGalleryBlockKind: {
                for (const [index, item] of block.images.entries()) {
                    if (
                        item.url &&
                        false === consumer(`${path}.images.${index}.url`, item.url, ImageGalleryBlockKind)
                    ) {
                        return;
                    }
                }

                break;
            }
            case HatBlockKind: {
                if (block.bgUrl && false === consumer(`${path}.bgUrl`, block.bgUrl, HatBlockKind)) {
                    return;
                }

                if (block.logoUrl && false === consumer(`${path}.logoUrl`, block.logoUrl, HatBlockKind)) {
                    return;
                }

                break;
            }

            case FeaturesBlockKind: {
                for (const [index, item] of block.items.entries()) {
                    if (
                        item.glyph.url &&
                        item.glyph.type === 'image' &&
                        false === consumer(`${path}.items.${index}.glyph.url`, item.glyph.url, FeaturesBlockKind)
                    ) {
                        return;
                    }
                }

                break;
            }

            case ServicesGridBlockKind: {
                for (const [index, item] of block.items.entries()) {
                    for (const [photoIndex, url] of item.photos.entries()) {
                        if (
                            false ===
                            consumer(`${path}.items.${index}.photos.${photoIndex}`, url, ServicesGridBlockKind)
                        ) {
                            return;
                        }
                    }
                }

                break;
            }
        }
    }
};

export const getPageThumbnailUrl = (page) => {
    let found = '';

    walkPageUrlAttributes(page, (key, url, kind) => {
        if (
            key === 'seo.ogImageUrl' ||
            (kind && (kind === ImageBlockKind || kind === ImageGalleryBlockKind || kind === HatBlockKind))
        ) {
            found = url;

            return false;
        }
    });

    return found;
};
